import {Action, applyMiddleware, combineReducers, compose, createStore} from "redux";
import thunk, { ThunkAction } from "redux-thunk";
import { AnalyticsReducer } from "./Admin/Analytics/AnalyticsReducer";
// Reducers
import { LocationsReducer } from "./Admin/Locations/LocationsReducer";
import { OperatorsReducer } from "./Admin/Operators/OperatorsReducer";
import { ImagesReducer } from "./Admin/Pictures/ImagesReducer";
import { SpecificationsReducer } from "./Admin/Specifications/SpecificationsReducer";
import { TypesReducer } from "./Admin/Types/TypesReducer";
import { AuthReducer } from "./Auth/AuthReducer";
import { UnapproveReducer } from "./Operator/Unapproved/UnapprovedReducer";

const rootReducer = combineReducers({
    auth: AuthReducer,
    unapproved: UnapproveReducer,
    operators: OperatorsReducer,
    specifications: SpecificationsReducer,
    locations: LocationsReducer,
    images: ImagesReducer,
    types: TypesReducer,
    analytics: AnalyticsReducer,
});

declare global {
    interface Window {
      __REDUX_DEVTOOLS_EXTENSION_COMPOSE__?: typeof compose;
    }
  }

// Store type
export type StateType = ReturnType<typeof rootReducer>;
// Типизация thunk
export type BaseThunkType<A extends Action, R = Promise<void>> = ThunkAction<R, StateType, unknown, A>;
// Actions type
export type InferActionsTypes<T> = T extends { [keys: string]: (...args: any[]) => infer U } ? U : never;
// Add Redux-devtool
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
// Create store with Thunk
export const store = createStore(rootReducer, composeEnhancers(applyMiddleware(thunk)));