import { InferActionsTypes } from "../store";


const AuthInitialState = {
    userData: {    
        phone: '',
        password: '',
        email: '',
        id: 0,
        role_id: null
    },
    isAuth: false,
    access_token: '',
    isLoading: false,
};


type authUserResponseDataType = {
    email: string,
    id: string,
    phone: string,
    role_id: number;
}

export const AuthActions =  {
    addUserData: (authUserData: authUserResponseDataType) => ({type: 'ADD_USER_DATA', authUserData} as const),
    addAccessToken: (access_token: string) => ({type: 'ADD_ACCESS_TOKEN', access_token} as const),
    changeIsLoading: () => ({type: 'CHANGE_IS_LOADING'} as const),
    changeRoleId: (roleId: number) => ({type: 'CHANGE_ROLE_ID', roleId} as const),
    logout: () => ({type: 'LOGOUT'} as const),
};

export const AuthReducer = (state = AuthInitialState, action: ActionsType ) => {
    switch (action.type) {
        case 'ADD_USER_DATA':
            return {
                ...state,
                userData: action.authUserData,
                isAuth: true
            };
        case 'ADD_ACCESS_TOKEN':
            return {
                ...state,
                access_token: action.access_token,
            };
        case 'CHANGE_IS_LOADING':
            return {
                ...state,
                isLoading: !state.isLoading,
            }
        case 'CHANGE_ROLE_ID':
            return {
                ...state,
                userData: {...state.userData, role_id: action.roleId}
            }
        case 'LOGOUT':
            return {
                ...state,
                userData: {    
                    phone: '',
                    password: '',
                    email: '',
                    id: 0,
                    role_id: 0
                },
                isAuth: false,
                access_token: '',
            }
        default:
            return state;
    }
};

// Store type
export type LoginInitialStateType = typeof AuthInitialState;

// Action types
export type ActionsType = InferActionsTypes<typeof AuthActions>;