import { InferActionsTypes } from '../../store';


// Store
const LocatiosState = {
    mainLocations: [],
    regionLocations: []
};


export type LocationsStateType = typeof LocatiosState;
export type LocationActionsType = InferActionsTypes<typeof LocationsActions>;


// Actions
export const LocationsActions = {
    addMainLocations: (mainLocations: LocationsType[]) => ({ type: 'ADD_MAIN_LOCATION', mainLocations } as const),
    addNewRegionLocations: (regionLocations: LocationsType) => ({ type: 'ADD_NEW_REGION_LOCATION', regionLocations } as const),
    addNewMainLocations: (mainLocations: LocationsType) => ({ type: 'ADD_NEW_MAIN_LOCATION', mainLocations } as const),
    addRegionLocations: (id: number, regionLocations: LocationsType[]) => ({ type: 'ADD_REGION_LOCATION', data: { id, regionLocations } } as const),
};


// Reducer
export const LocationsReducer = (state = LocatiosState, action: LocationActionsType) => {
    switch (action.type) {
        case 'ADD_MAIN_LOCATION':
            return {
                ...state,
                mainLocations: action.mainLocations
            };
        case 'ADD_REGION_LOCATION':
            return {
                ...state,
                regionLocations: action.data.regionLocations
            };
        case 'ADD_NEW_REGION_LOCATION':
            return {
                ...state,
                regionLocations: [...state.regionLocations, action.regionLocations]
            }
        case 'ADD_NEW_MAIN_LOCATION':
            return {
                ...state,
                mainLocations: [...state.mainLocations, action.mainLocations]
            }
        default:
            return state;
    }
};


// Types
type LocationsType = {
    id: number;
    name: string
}