import { ThunkAction } from "redux-thunk";
import { api } from "../../api";
import { ActionsType, AuthActions, LoginInitialStateType } from "./AuthReducer";
import Cookies from 'js-cookie';

// User login data
type userLoginData = {
    phone: string,
    password: string
};

export const loginThunk = (data: userLoginData): ThunkAction<Promise<void>, {}, LoginInitialStateType, ActionsType> => {
    return async (dispatch: any) => {
        try {
            dispatch(AuthActions.changeIsLoading());
            debugger;
            const response = await api.postApi( {url: 'admin/login', token: '', withCredentials: true, params: {phone: data.phone, password: data.password} });
            debugger;
            Cookies.set('refresh_token', response.data.refresh_token);
            dispatch(AuthActions.addUserData(response.data.data));
            dispatch(AuthActions.addAccessToken(response.data.access_token));
            dispatch(AuthActions.changeIsLoading());
            return response;
        } catch (error: any) {
            console.log(error.response.message);
            debugger;
            dispatch(AuthActions.changeIsLoading());
            return error.response;
        }
    };
};

export const loadOperatorThunk = () => {
    return async (dispatch: any) => {
        const token = Cookies.get('refresh_token');
        if(token){
            try {
                const response = await api.getApi({url:'operator/load-operator', token: token, withCredentials:true, contentType: false});
                dispatch(AuthActions.changeRoleId(response.data.data.role_id));
                dispatch(AuthActions.addUserData(response.data.data));
                dispatch(AuthActions.addAccessToken(response.data.access_token));
            } catch (e) {
                console.log(e);
            }
        } else {
            dispatch(AuthActions.changeRoleId(0));
            return
        }

    }
};

