import { InferActionsTypes } from "../../store";


// Store
const AnalyticsState = {
    analyticsData: null,
    searchedAnalyticsData: null,
};


// Types
export type AnalyticsData = {id: number; count: string; absolute_name: string};


// Actions
export const AnalyticsActions = {
    addAnalyticsData: (analyticsData: AnalyticsData[]) => ({ type: 'ADD_ANALYTICS_DATA', analyticsData } as const),
};


export const AnalyticsReducer = (state = AnalyticsState, action: AnalyticsActionsType) => {
    switch (action.type) {
        case 'ADD_ANALYTICS_DATA':
            return {
                ...state,
                analyticsData: action.analyticsData
            }
        default:
            return state;
    }
};


export type AnalyticsStateType = typeof AnalyticsState;
export type AnalyticsActionsType = InferActionsTypes<typeof AnalyticsActions>;