// Libraries
import { useState } from 'react';
import { FormProvider, useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
// Components
import {loginThunk} from '../../Redux/Auth/AuthThunks';
import { PhoneInput } from '../../Components/Inputs/PhoneInput/PhoneInput';
import { PasswordInput } from '../../Components/Inputs/PasswordInput/PasswordInput';
// Selectors
import { GetIsLoading } from '../../Redux/Auth/AuthSelector';
// Styles
import styles from './Auth.module.css';
// Images
import { Spinner } from '../../Components/Spinner/Spinner';


export default function Auth() {
    const dispatch = useDispatch();

    const isLoading = useSelector(GetIsLoading);
    
    const [error_message] = useState(null);

    const onFormSubmit = (data: { phone: string; password: string; }) => {
        dispatch(loginThunk(data));
    };   

    const methods = useForm<{ phone: string; password: string; }>();
    const { handleSubmit } = methods;
    return (
        <>
            {isLoading ? <Spinner /> : 
                <div className={styles.main_block}>
                    <div className={styles.content_block}>
                        
                        <h1 className={styles.title}> Login </h1>
                        <FormProvider {...methods}>
                            <form id='login_form' className={styles.form} onSubmit={handleSubmit(onFormSubmit)}>
                                <PhoneInput />
                                <PasswordInput />
                                <p className={`text-center ${styles.error_message}`}>{error_message}</p>
                                <div className={`${styles.login_button_block}`}>
                                    <button className={styles.button}>Log in</button>
                                </div>
                            </form>
                        </FormProvider>
                    </div>
                </div>
            }
        </>
    )
};