import { useState } from 'react';
import { useFormContext } from 'react-hook-form';
import * as yup from 'yup';
// Images
import eye from '../../../Images/eye.png';
import lock from '../../../Images/lock.png';
//Styles
import styles from './PasswordInput.module.css';


export const PasswordInput = () => {

    // Local state
    const [password_shown, setPassword_shown] = useState(false);

    // Global state
    const { register, formState: { errors }} = useFormContext();

    // // Functions
    // const validationSchema = yup.object().shape({
    //     password: yup.string()
    //     .test('PasswordRequirement', 'Information', (value: any) => 
    //         [/[a-z]/, /[A-Z]/, /[0-9]/, /[^a-zA-Z0-9]/].every((pattern) =>
    //             pattern.test(value)
    //         )
    //     )
    // });

    return (
        <div className={styles.password_block}>
            <img alt='Lock' src={lock} className={styles.lock_image}/>
            <img alt='Eye' src={eye} onClick={() => setPassword_shown(!password_shown)} className={styles.eye_image}/>
            <input id='password'
                type={password_shown ? 'text' : 'password'}
                placeholder='Password'
                {...register('password', {pattern: /^[a-zA-Z0-9]+$/, required: true, minLength: 8})}
            />
            {errors.password?.type === 'required' && <p className={styles.error_message}>Пароль обязателен</p>}
            {errors.password?.type === 'pattern' && <p className={`${styles.error_message} text-center`}>
                Пароль должен состоять из букв и хотя бы одной цифры
            </p>}
            {errors.password?.type === 'minLength' && <p className={styles.error_message}>Пароль не должен быть меньше 8 символов</p>}
        </div>
    )
};