import React, { Suspense, useEffect } from 'react';
import { Toaster } from 'react-hot-toast';
import { useDispatch, useSelector } from 'react-redux';
import { Redirect, Route, Switch } from 'react-router';
import { Spinner } from './Components/Spinner/Spinner';
import Auth from './Pages/Auth/Auth';
import { GetRoleId } from './Redux/Auth/AuthSelector';
import { loadAdminThunk, loadOperatorThunk } from './Redux/Auth/AuthThunks';

const AdminIndex = React.lazy(() => import('./Pages/Admin/AdminIndex'));
const OperatorIndex = React.lazy(() => import('./Pages/Operator/OperatorIndex'));

export default function App() {
  const dispatch = useDispatch();
  const role_id = useSelector(GetRoleId);
  console.log(role_id);
  
  useEffect(() => {
    dispatch(loadOperatorThunk());
    dispatch(loadAdminThunk());
  }, [dispatch]);

    return (
      <>
          <div className="App">
            <Suspense fallback={<div>Loading...</div>}>
            {
              (role_id === 1)
              ?
                <Switch>
                  <Route path='/admin' component={AdminIndex}/> 
                  <Redirect to='/admin/operators'/>
                </Switch>
              : (role_id === 2)
                ?
                  <Switch>
                    <Route path='/operator' component={OperatorIndex} />
                    <Redirect to='/operator/unapproved'/>
                  </Switch>
                : (role_id === 0)
                  ?
                    <>
                      <Route path='/auth' component={Auth} />
                      <Redirect to='/auth' />
                    </>
                  : <Spinner />
            }
            <Toaster />
            </Suspense>
          </div>
      </>
    )
}