import { useFormContext } from 'react-hook-form';
import styles from './PhoneInput.module.css';

export const PhoneInput = () => {
    const { register, formState: { errors } } = useFormContext();
    return (
        <div className={`relative ${styles.phone_block}`}>
            <div className={`${styles.phone_prefix}`}>993</div>
            <input
                placeholder='6xxxxxxx'
                type='number'
                {...register('phone', {required: true, minLength: 8, maxLength: 8,})}
            />
            {errors.phone?.type === 'required' && <p className={styles.error_message}>Телефон обязателен</p>}
            {errors.phone?.type === 'minLength' && <p className={styles.error_message}>Номер телефона должен состоять из 8 цифр</p>}
            {errors.phone?.type === 'maxLength' && <p className={styles.error_message}>Номер телефона должен состоять из 8 цифр</p>}
        </div>
    );
};