import axios from 'axios';
import Cookies from 'js-cookie';
// 109.106.244.215
// Loсal 192.168.31.30
const axiosInstance = axios.create({
    baseURL: `${process.env.REACT_APP_BASE_URL}/api/`,
    timeout: 10000,
    headers: {
        'Content-Type': 'application/json',
        accept: 'application/json',
        Authorization: null
    }
});

axiosInstance.interceptors.response.use(
    response => response,
    (error) => {
        const originalRequest = error.config;
        if (error.response.status === 401 && error.response.data === 'Unauthorized') {
            const config = {
                headers: {
                    'Content-Type': 'application/json',
                    accept: 'application/json',
                    Authorization: `Bearer ${Cookies.get('refresh_token')}`,
                },
                withCredentials: true
            }
            return axiosInstance.get('/auth/token/refresh/', config)
                .then((response) => {
                    axiosInstance.defaults.headers.Authorization = `Bearer ${response.data.token}`;
                    originalRequest.headers.Authorization = `Bearer ${response.data.token}`;
                    return axiosInstance(originalRequest);
                }).catch((err) => {
                    axiosInstance.defaults.headers.Authorization = null;
                    originalRequest.headers.Authorization = null;
                    return Promise.reject(error);
                });
        } else {
            axiosInstance.defaults.headers.Authorization = null;
            originalRequest.headers.Authorization = null;
            return Promise.reject(error);
        }
    }
);

export const api = {
    getApi: async ({ url, token, withCredentials, contentType }) => {
        const config = {
            headers: {
                'Content-Type': contentType ? contentType : 'application/json',
                accept: 'application/json',
                Authorization: `Bearer ${token}`,
            },
            timeout: contentType ? 100000 : 20000,
            withCredentials
        }
        const response = await axiosInstance.get(`${url}`, config);
        return response;
    },
    postApi: async ({ url, token, withCredentials, params }) => {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                accept: 'application/json',
                Authorization: `Bearer ${token}`,
            },
            withCredentials
        }
        const response = await axiosInstance.post(`${url}`, params, config);
        return response;
    },
    uploadPhoto: async ({ url, token, withCredentials, formData }) => {
        const config = {
            headers: {
                'Content-Type': 'multipart/form-data',
                Authorization: `Bearer ${token}`
            },
            timeout: 50000,
            withCredentials: withCredentials,
        };
        const response = await axiosInstance.post(`${url}`, formData, config);
        return response;
    }
}

export default axiosInstance;