import { InferActionsTypes } from '../../store';


const TypesInitialState = {
    types: null,
    type: null  as unknown as TypeType,
    active_type_specifications: null as unknown as Array<TypeSpecificationType>,
    deleted_type_specifications: null as unknown as Array<TypeSpecificationType>,
    unusedSpecs: null as unknown as Array<UnusedSpecType>,
    wannaUseSpecs: [] as unknown as Array<UnusedSpecType>,
};


// Types
export type TypesType = {
    id: number;
    main_type: string;
    absolute_name: string;
    destination: null | string;
    translations: Array<TranslationType>;
};

export type TypeType = {
    absolute_name: string;
    destination: null;
    translations: Array<{
        name: string;
        language_id: number;
    }>;
    active_type_specifications: null | Array<TypeSpecificationType>;
    deleted_type_specifications: null | Array<TypeSpecificationType>;
};

export type TypeSpecificationType = {
    id: number;
    absolute_name: string;
    deleted: boolean;
    type_spec_id: number;
    queue_position: number;
    spec_translations: Array<TranslationType>
};

export type TranslationType = {
    language_id: number;
    name: string;
};

export type UnusedSpecType = {
    spec_id: number;
    absolute_name: string;
    spec_translations: TranslationType[];
}

export const TypesActions =  {
    addTypes: (types: TypesType) => ({type: 'ADD_TYPES', types} as const),
    addType: (typeData: TypeType) => ({type: 'ADD_TYPE', typeData} as const),
    changeDeleted: (boolValue: boolean, id: number) => ({type: 'CHANGE_DELETED_VALUE', data: {id, boolValue}} as const),
    switchTrueToFalseSpecification: (value: any) => ({type: 'SWITCH_TRUE_TO_FALSE_SPECIFICATION', value} as const),
    switchFalseToTrueSpecification: (value: any) => ({type: 'SWITCH_FALSE_TO_TRUE_SPECIFICATION', value} as const),
    addUnusedSpecToType: (unusedSpecs: Array<UnusedSpecType>) => ({type: 'ADD_UNUSED_SPEC', unusedSpecs} as const),
    addCreatedSpecToUnused: (createdSpec: UnusedSpecType) => ({type: 'ADD_CREATED_SPEC_TO_UNUSED', createdSpec} as const),
    changeUnusedToWannaUse: (spec: UnusedSpecType) => ({type: 'CHAGE_UNUSED_SPEC_TO_WANNA_USE', spec} as const),
    changeWannaUseToUnused: (spec: UnusedSpecType) => ({type: 'CHAGE_WANNA_USE_TO_UNUSED_USE', spec} as const),
};


export const TypesReducer = (state = TypesInitialState, action: TypeActionsType) => {
    switch (action.type) {
        case 'ADD_TYPES':
            return {
                ...state,
                types: action.types,
            };
        case 'ADD_TYPE':
            return {
                ...state,
                type: action.typeData,
                active_type_specifications: action.typeData.active_type_specifications,
                deleted_type_specifications: action.typeData.deleted_type_specifications,
            };
        case 'SWITCH_TRUE_TO_FALSE_SPECIFICATION':
            return {
                ...state,
                active_type_specifications: state.active_type_specifications.filter((value: TypeSpecificationType) => value.type_spec_id !== action.value.type_spec_id),
                deleted_type_specifications: [...state.deleted_type_specifications, action.value]
            };
        case 'SWITCH_FALSE_TO_TRUE_SPECIFICATION':
            return {
                ...state,
                deleted_type_specifications: state.deleted_type_specifications.filter((value: TypeSpecificationType) => value.type_spec_id !== action.value.type_spec_id),
                active_type_specifications: [...state.active_type_specifications, action.value]
            };
        case 'ADD_UNUSED_SPEC':
            return {
                ...state,
                unusedSpecs: action.unusedSpecs
            };
        case 'CHAGE_UNUSED_SPEC_TO_WANNA_USE':
            return {
                ...state,
                unusedSpecs: state.unusedSpecs.filter((unusedSpec: UnusedSpecType) => unusedSpec.spec_id !== action.spec.spec_id),
                wannaUseSpecs: [...state.wannaUseSpecs, Object.assign(action.spec, {position: null})]
            };
        case 'CHAGE_WANNA_USE_TO_UNUSED_USE':
            return {
                ...state,
                wannaUseSpecs: state.wannaUseSpecs.filter((wannaUseSpec: UnusedSpecType) => wannaUseSpec.spec_id !== action.spec.spec_id),
                unusedSpecs: [...state.unusedSpecs, action.spec]
            };
        case 'ADD_CREATED_SPEC_TO_UNUSED':
            return {
                ...state,
                unusedSpecs: [...state.unusedSpecs, action.createdSpec]
            };
        default:
            return state;
    }
};


// Store type
export type TypesInitialStateType = typeof TypesInitialState;

// Action types
export type TypeActionsType = InferActionsTypes<typeof TypesActions>;