import { InferActionsTypes } from '../../store';


const AuthInitialState = {
    estates: [],
    estate: [],
    deleteEstate: null,
};


export const UnapproveActions =  {
    addEstatesData: (estates: estatesDataType) => ({type: 'ADD_ESTATES_DATA', estates} as const),
    addEstateData: (estate: estateDataType) => ({type: 'ADD_ESTATE_DATA', estate} as const),
    addDeleteEstate: (id: number) => ({type: 'ADD_DELETE_ID', id} as const),
};


export const UnapproveReducer = (state = AuthInitialState, actions: ActionsType ) => {
    switch (actions.type) {
        case 'ADD_ESTATES_DATA':
            return {
                ...state,
                estates: actions.estates,
            };
        case 'ADD_ESTATE_DATA':
            return {
                ...state,
                estate: actions.estate,
            };
        case 'ADD_DELETE_ID':
            return {
                ...state,
                deleteEstate: actions.id,
            };   
        default:
            return state;
    }
};


// Types
export type estatesDataType = {
    id: number;
    price: string,
    location: string,
    real_estate_name: string,
    image: string,
};

type estateDataType = {
    area: string,
    created_at: string,
    description: string,
    images: string | null,
    location: string,
    owner_type: string,
    phone: string,
    price: string,
    real_estate_name: string,
    specifications: [
        {
            name: string,
            values: [
                {
                    absolute_value: string,
                    name: null
                }
            ]
        }
    ],
    view_counts: string,
};

export type LoginInitialStateType = typeof AuthInitialState;

export type ActionsType = InferActionsTypes<typeof UnapproveActions>;