import { InferActionsTypes } from '../../store';


const ImagesInitialState = {
    imagesLocations: null,
    imageLocations: null as any,
};

export type LocationImagesType = {
    id: number;
    destination: string;
}

export const ImagesActions =  {
    addImagesLocations: (imagesLocations: Array<{id: number; place: string}>) => ({type: 'ADD_IMAGES_LOCATIONS', imagesLocations} as const),
    addLocationImages: (locationImages: Array<LocationImagesType>) => ({type: 'ADD_LOCATION_IMAGES', locationImages} as const),
    addImage: (image: {id: number; destination: string}) => ({type: 'ADD_IMAGE', image} as const),
};


export const ImagesReducer = (state = ImagesInitialState, action: ActionsType ) => {
    switch (action.type) {
        case 'ADD_IMAGES_LOCATIONS':
            return {
                ...state,
                imagesLocations: action.imagesLocations
            };
        case 'ADD_LOCATION_IMAGES':
            return {
                ...state,
                imageLocations: action.locationImages
            };
        case 'ADD_IMAGE':
            return {
                ...state,
                imageLocations: [...state.imageLocations, action.image]
            };
        default:
            return state;
    }
};


// Store type
export type PicturesInitialStateType = typeof ImagesInitialState;

// Action types
export type ActionsType = InferActionsTypes<typeof ImagesActions>;